<template>
  <div>
    <div class="row">
      <div class="col-md-11 col-sm-6">
        <h3 class="fw-bold">Add Reseller</h3>
      </div>
      <div class="col-md-1 col-sm-6 my-2">
        <router-link to="/admin/resellers">
          <v-btn color="error">
            <v-icon left class="mt-1">mdi-format-list-bulleted</v-icon>List</v-btn>
        </router-link>
      </div>
    </div>
    <div class="my-5">
      <v-form @submit.prevent="resellerAddFormSubmit" ref="form" v-model="valid" lazy-validation>
        <v-container>
          <v-row>
            <v-col cols="12" sm="6" md="12">
              <v-text-field 
                  v-model="formData.name"
                  label="Name" 
                  prepend-icon="mdi-account"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field 
                  v-model="formData.email"
                  @keyup="emailVerify"
                  :error-messages="emailVerifyMessage"
                  label="E-mail"
                  prepend-icon="mdi-email"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field prepend-icon="mdi-lock" 
                  v-model="formData.password"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  :passrules="[passrules.required, passrules.min]" :type="show1 ? 'text' : 'password'"
                  name="input-10-1" label="Password" hint="At least 8 characters" counter
                  @click:append="show1 = !show1"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-select
                  v-model="formData.reseller_gender"
                  :rules="genderRules"
                  label="Select Gender"
                  :items="genders"
                  item-text="gender"
                  item-value="gender"
                  prepend-icon="mdi-human"></v-select>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-text-field
                  v-model="formData.reseller_dob"
                  type="date"
                  label="Date of birth"
                  prepend-icon="mdi-calendar" multiple>
              </v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-text-field prepend-icon="mdi-phone" 
                  type="number"
                  :counter="10"
                  v-model="formData.mobile_phone" 
                  @keyup="phoneVerify"
                  :error-messages="phoneVerifyMessage"
                  label="Phone Number"></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-autocomplete
                  v-model="formData.profession_id"
                  label="Select Profession"
                  :items="allProfessions"
                  item-text="profession"
                  item-value="id"
                  prepend-icon="mdi-tray-arrow-down">
              </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
              <v-autocomplete 
                  v-model="formData.addedSkills" 
                  :items="allSkills" 
                  item-text="skill" 
                  item-value="id"
                  multiple chips  
                  label="Skills & Experience" 
                  prepend-icon="mdi-download"></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
                <v-autocomplete
                  v-model="formData.state_id"
                  @change="getCities"
                  :items="allstates"
                  label="Select State"
                  item-text="state"
                  item-value="id"
                  prepend-icon="mdi-account">
              </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="6">
                <v-autocomplete
                  v-model="formData.city_id"
                  :items="cities"
                  label="Select City"
                  item-text="city"
                  item-value="id"
                  prepend-icon="mdi-city" >
                </v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="12">
              <v-textarea 
                  v-model="formData.address"
                  label="Address" 
                  prepend-icon="mdi-download"
                  rows="2"></v-textarea>
            </v-col>
          </v-row>
          <v-col cols="12" md="12" class="py-0 d-flex" style="justify-content: space-between;">
                <div>
                  <!-- <v-checkbox v-model="formData.user_status" label="Active"></v-checkbox> -->
                </div>
                <div class="ms-3 pb-3">
                    <v-btn v-if="isLoading" class="my-2 me-3 cred">Loading...</v-btn>
                    <v-btn v-else class="my-2 me-3 cred" type="submit" :disabled="!valid">SAVE</v-btn>
                </div>
          </v-col>

        </v-container>
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout" type="success" border="left" elevation="3">
      <template v-slot:action="{ attrs }">
        <v-btn color="blue"  v-bind="attrs" text @click="snackbar = false">Close</v-btn>
      </template>{{text}}
    </v-snackbar>
  </div>
</template>

<script>
import axios from "axios";
export default {
    name: "ResellersNew",
    components: {  },
    data() {
        return {
            genders: [{id:1,gender:'Male'},{id:2,gender:'Female'}],
            allstates: [],
            cities: [],
            allProfessions:[],
            allSkills: [],
            isLoading: false,
            show1: false,
            password: '',
            passrules: {
                required: value => !!value || 'Required.',
                min: v => v.length >= 8 || 'Min 8 characters',
            },
            emailVerifyMessage:'',
            userNameVerifyMessage:'',
            phoneVerifyMessage: '',
            formData:{},
            snackbar: false,
            text: '',
            timeout: '',
        }
    },
    mounted() {
      this.All_States();
      this.getCities();
      this.All_Professions();
      this.All_skills();
    },
    methods:{
        All_States(){
          axios.get('allstates')
            .then((resp)=>{
              this.allstates = resp.data.locations;
            });
        },
        getCities() {
          if (this.formData.state_id) {
            axios.get('states/'+this.formData.state_id)
                .then(response => {
                  this.cities = response.data;
                })
          } else {
            this.cities = [];
          }
        },
        All_Professions(){
          axios.get('professions')
            .then((resp)=>{
              this.allProfessions = resp.data.professions;
            });
        },
        All_skills(){
          axios.get('skills')
            .then((resp)=>{
              this.allSkills = resp.data.skills;
            });
        },
        emailVerify(){ // verify email
          if(/.+@.+/.test(this.formData.email)){
              axios.post('user/verify', {'email':this.formData.email})
                  .then((resp) => {
                    if(resp.data.status==400){
                        this.emailVerifyMessage = resp.data.message;
                    }else{
                        this.emailVerifyMessage = "";
                    }
              });
            }
        },
        phoneVerify(){ // verify phone
          if(this.formData.mobile_phone.length >= 9){
              axios.post('user/verify', {'mobile_phone':this.formData.mobile_phone})
                  .then((resp) => {
                    if(resp.data.status==400){
                        this.phoneVerifyMessage = resp.data.message;
                    }else{
                        this.phoneVerifyMessage = "";
                    }
              });
          }
        },
        resellerAddFormSubmit() {
            const config = {
                headers: { 'content-type': 'multipart/form-data' }
            }
            let data = { 
                name: this.formData.name,
                reseller_gender: this.formData.reseller_gender,
                email: this.formData.email,
                password: this.formData.password,
                reseller_dob: this.formData.reseller_dob,
                mobile_phone: this.formData.mobile_phone,
                profession_id: this.formData.profession_id,
                skills_id: this.formData.addedSkills,
                state_id: this.formData.state_id,
                city_id: this.formData.city_id,
                address: this.formData.address,
                user_status: 1,
              };
            if( this.formData.name!=null && this.formData.reseller_gender!=null && this.formData.email!=null && this.formData.password!=null && this.formData.reseller_dob!=null && this.formData.mobile_phone!=null && this.formData.profession_id!=null && this.formData.addedSkills!=null && this.formData.state_id!=null && this.formData.city_id!=null && this.formData.address!=null)
            {
                this.isLoading = true;
                axios.post("admin/reseller/add", data, config)
                  .then((resp) => {
                    this.isLoading = false;
                    this.snackbar = true;
                    this.text = resp.data.message;
                    this.timeout = 3000;
                    this.$refs.form.reset();
                })
            }else{
              this.snackbar = true;
              this.text = "All input filds are required.";
              this.timeout = 3000;
              this.isLoading = false;
            }
        },
    }
}
</script>